import React, { useState, useEffect } from 'react'
import strategy from '../../images/strategy.png'
import Arrow from '../../images/Arrow.png'
import pendingDot from '../../images/pendingDot.png'
import axios from 'axios'
import { useSelector, useDispatch } from 'react-redux'
import { checkTokenExpired } from '../../globalState/actions/AuthActions'
import { useHistory } from 'react-router'
import GreenDot from '../../images/greendot.png'
import EditCourseForm from './EditCourseForm'
import { backendLink } from '../../keys_dev'
//import { checkTokenExpired } from '../../../globalState/actions/AuthActions'

export default function AddedCourses(props) {
  const [existingCourses, setExistingCourses] = useState([])
  const [openCourse, setOpenCourse] = useState([])
  const [loading, setLoading] = useState(true)
  //const [viewCourse, setViewCourse] = useState([])
  const token = useSelector((state) => state.token)
  const id = useSelector((state) => state.id)
  const accountId = useSelector((state) => state.id)
  const dispatch = useDispatch()
  const history = useHistory()
  const [flagT, setFlagT] = useState(false)

  useEffect(() => {
    if (!dispatch(checkTokenExpired(history))) {
      axios({
        url: `${backendLink}/tbhapp/courses/viewAllCourses`,
        method: 'POST',
        headers: {
          authorization: token,
        },
        data: {
          Account: {
            id,
          },
          accountId,
        },
      })
        .then((res) => {
          console.log(res)
          if (res.data.statusCode === 0) {
            setExistingCourses(res.data.AllCourses)
            let y = []
            y.length = res.data.AllCourses.length
            console.log(y)
            res.data.AllCourses.map((element, index) => {
              y[index] = false
            })
            setOpenCourse(y)
            setLoading(false)
          }
        })
        .catch((error) => {
          console.log(error)
        })
      // }
    }
  }, [props.flag, flagT])

  return (
    <div>
      {console.log(openCourse)}
      {loading ? (
        <div></div>
      ) : (
        <div>
          {existingCourses.map((item, index) => {
            return (
              <div
                style={{
                  border: 'solid',
                  borderColor: '#D1D1D1',
                  // marginTop: '1vw',
                  marginLeft: '5vw',

                  marginBottom: '1vw',
                  width: '50vw',
                }}
              >
                {console.log(index)}
                {openCourse[index] === false ? (
                  <table style={{ width: '100%' }}>
                    {console.log('here2')}
                    <td style={{ width: '80%' }}>
                      <tr></tr>
                      <tr>
                        <td style={{ border: 'none' }}>
                          <img
                            style={{
                              padding: '0',
                              width: '5vw',
                              height: '5vw',
                              paddingBottom: '0',
                            }}
                            src={strategy}
                            alt=""
                          />
                          <text
                            style={{
                              fontWeight: 'bold',
                              fontSize: '1.3vw',
                              // marginRight: '37vw',
                              verticalAlign: 'middle',
                            }}
                          >
                            {item.title}
                          </text>
                        </td>
                      </tr>
                    </td>
                    <td
                      style={{
                        width: '30%',
                        fontSize: '1vw',
                      }}
                    >
                      <td>
                        <tr>
                          {item.State === 'approved' ? (
                            <img
                              src={GreenDot}
                              alt=""
                              style={{ width: '1vw', height: '1vw' }}
                            />
                          ) : (
                            <img src={pendingDot} alt="" />
                          )}
                          &nbsp; {item.State.toUpperCase()}
                        </tr>
                        <br></br>
                        <tr>
                          {openCourse[index] === false ? (
                            <button
                              style={{
                                color: '#ED1C24',
                                backgroundColor: 'white',
                                border: 'none',
                                marginLeft: '1vw',
                                fontWeight: 'bold',
                                fontSize: '1vw',
                              }}
                              onClick={() => {
                                const x = [...openCourse]
                                console.log(x[index])
                                x[index] = true
                                setOpenCourse(x)
                              }}
                            >
                              Show More &nbsp;
                              <img src={Arrow} alt="" />
                            </button>
                          ) : null}
                        </tr>
                      </td>
                    </td>
                    <tr></tr>
                  </table>
                ) : (
                  <div>
                    {console.log('here')}
                    <table style={{ width: '100%' }}>
                      {console.log('here2')}
                      <td style={{ width: '80%' }}>
                        <tr></tr>
                        <tr>
                          <td style={{ border: 'none' }}>
                            <img
                              style={{
                                padding: '0',
                                width: '5vw',
                                height: '5vw',
                                paddingBottom: '0',
                              }}
                              src={strategy}
                              alt=""
                            />
                            <text
                              style={{
                                fontWeight: 'bold',
                                fontSize: '1.3vw',
                                // marginRight: '37vw',
                                verticalAlign: 'middle',
                              }}
                            >
                              {item.title}
                            </text>
                          </td>
                          {/* <td
                    style={{
                      textAlign: 'left',
                      verticalAlign: 'middle',
                      border: 'none',
                    }}
                  >

                  </td> */}
                        </tr>
                      </td>
                      <td style={{ width: '30%' }}>
                        <td>
                          <tr>
                            {item.State === 'approved' ? (
                              <img
                                src={GreenDot}
                                alt=""
                                style={{ width: '1vw', height: '1vw' }}
                              />
                            ) : (
                              <img src={pendingDot} alt="" />
                            )}
                            &nbsp; {item.State.toUpperCase()}
                          </tr>
                          <br></br>
                          <tr>
                            {openCourse[index] === false ? (
                              <button
                                style={{
                                  color: '#ED1C24',
                                  backgroundColor: 'white',
                                  border: 'none',
                                  marginLeft: '1vw',
                                  fontWeight: 'bold',
                                  fontSize: '1vw',
                                }}
                                onClick={() => {
                                  const x = [...openCourse]
                                  console.log(x[index])
                                  x[index] = true
                                  setOpenCourse(x)
                                }}
                              >
                                Show More &nbsp;
                                <img src={Arrow} alt="" />
                              </button>
                            ) : null}
                          </tr>
                        </td>
                      </td>
                      <tr></tr>
                    </table>

                    <EditCourseForm
                      id={item.id}
                      title={item.title}
                      eventTitle={item.eventTitle}
                      description={item.description}
                      category={item.category}
                      courseMaterial={item.courseMaterial}
                      uploadFile={item.uploadFile}
                      totalDurationInHours={item.durationInHours}
                      numberOfSessions={item.daysPerWeek}
                      sessionDuration={item.sessionDuration}
                      sessionsPerWeek={item.daysPerWeek}
                      minNumberOfAttendees={item.minNumberOfAttendees}
                      maxNumberOfAttendees={item.maxNumberOfAttendees}
                      estimatedPrice={item.pricePerPerson}
                      handleClose={() => {
                        const x = [...openCourse]
                        console.log(x[index])
                        x[index] = false
                        setOpenCourse(x)
                        setFlagT(flagT)
                      }}
                    />
                  </div>
                )}
              </div>
            )
          })}
        </div>
      )}
    </div>
  )
}
