export default function ValidationModal(props) {
  const token = useSelector((state) => state.token)
  const id = useSelector((state) => state.id)
  const { room, startDate, slots, payment, price } = props
  let openClose = props.open
  const classes = useStyles()

  function get() {
    if (props.showRoomLayout.aroundTable === 'true') return 'around table'
    if (props.showRoomLayout.grid === 'true') return 'grid'
    if (props.showRoomLayout.uShape === 'true') return 'uShape'
    if (props.showRoomLayout.cozy === 'true') return 'cozy'
  }
  const closealert = () => {
    setShowAlert(false)
  }
  const [showAlert, setShowAlert] = React.useState(false)
  const [showAlertMess, setShowAlertMess] = React.useState('error')
  const [showCounter, setCounter] = React.useState(0)
  const layouts = [layout1, layout2, layout3, layout4]
  const layoutNames = ['GRID', 'U-SHAPE', 'COZY', 'AROUND THE TABLE']

  let smallImages = []
  let largeImage = []
  for (let i = 0; i < layouts.length; i++) {
    if (showCounter % 4 !== i) {
      smallImages.push(
        <td>
          <SmallImage img={layouts[i]} name={layoutNames[i]} />
        </td>
      )
    } else {
      largeImage.push(<MainImage img={layouts[i]} name={layoutNames[i]} />)
    }
  }

  return (
    <div>
      <Dialog
        open={openClose}
        aria-labelledby='alert-dialog-title'
        aria-describedby='alert-dialog-description'
        classes={{ paper: classes.paper }}
      >
        <DialogTitle
          style={{
            width: '100%',
            height: ' 3vw',
            display: 'flex',
            padding: 0,
            justifyContent: 'flex-end',
          }}
        >
          <IconButton
            aria-label='close'
            className={classes.notClickedButton}
            onClick={() => {
              props.handleClose(false)
            }}
          >
            <CloseIcon />
          </IconButton>
        </DialogTitle>

        <DialogContent
          style={{
            width: '100%',
            height: ' 5vw',
            display: 'flex',
            justifyContent: 'flex-end',
            alignItems: 'bottom',

            padding: '0',
            backgroundImage: `url(${headerLayouts})`,
            backgroundRepeat: 'no-repeat',
            backgroundSize: '68vw',
            backgroundPositionX: 'center',
            backgroundPositionY: 'top',
          }}
        >
          <table>
            <td style={{}}>
              <LeftButton />
            </td>
            <td>
              <tr>
                {smallImages} <td> {largeImage}</td>
              </tr>
            </td>

            <td>
              <RightButton />
            </td>
          </table>
        </DialogContent>

        <DialogActions
          style={{
            width: '100%',
            display: 'flex',
            justifyContent: 'flex-end',
          }}
        ></DialogActions>
      </Dialog>
    </div>
  )
  function MainImage(props) {
    return (
      <table style={{ marginTop: '9vw', marginLeft: '2vw' }}>
        <tr>
          <img src={props.img} className={classes.mainImageStyle} />
        </tr>
        <tr>
          <Typography className={classes.mainImageTypographyStyle}>
            {props.name}
          </Typography>
        </tr>
      </table>
    )
  }
  function RightButton() {
    return (
      <Button
        onClick={() => {
          setCounter(showCounter + 1)
        }}
      >
        <img src={rightArrow} className={classes.arrowStyle} />
      </Button>
    )
  }
  function LeftButton() {
    return (
      <Button
        onClick={() => {
          setCounter(showCounter - 1)
        }}
      >
        <img src={leftArrow} className={classes.arrowStyle} />
      </Button>
    )
  }
  function SmallImage(props) {
    return (
      <table>
        <tr>
          <img src={props.img} className={classes.smallImageStyle} />
        </tr>
        <tr>
          <Typography className={classes.smallImageTypographyStyle}>
            {props.name}
          </Typography>
        </tr>
      </table>
    )
  }
}
