  function handleSlotsNameChange() {
    const newSlots = [
      ' [09  10]',
      ' [10  11]',
      ' [11  12]',
      ' [12  13]',
      ' [13  14]',
      ' [14  15]',
      ' [15  16]',
      ' [16  17]',
      ' [17  18]',
      ' [18  19]',
      ' [19  20]',
      ' [20  21]',
    ]
    let oldSlots = [...slotsNamesSelected]

    let slots = [
      'NINE_TEN',
      'TEN_ELEVEN',
      'ELEVEN_TWELVE',
      'TWELVE_THIRTEEN',
      'THIRTEEN_FOURTEEN',
      'FOURTEEN_FIFTEEN',
      'FIFTEEN_SIXTEEN',
      'SIXTEEN_SEVENTEEN',
      'SEVENTEEN_EIGHTEEN',
      'EIGHTEEN_NINETEEN',
      'NINETEEN_TWENTY',
      'TWENTY_TWENTYONE',
    ]
    let returnList = []
    oldSlots.map((slot) => {
      returnList.push(newSlots[slots.indexOf(slot)])
    })
    returnList = returnList.sort()
    console.log(returnList.sort())
    const newSlots2 = [
      '9 AM',
      '10 AM',
      '11 AM',
      '12 PM',
      '1 PM',
      '2 PM',
      '3 PM',
      '4 PM',
      '5 PM',
      '6 PM',
      '7 PM',
      '8 PM',
      '9 PM',
    ]
    if (oldSlots.length === 0) return ''
    if (oldSlots.length === 1) {
      return newSlots2[slots.indexOf(oldSlots[0])]
    } else {
      let firstSlot = newSlots2[newSlots.indexOf(returnList[0])]
      let lastSlot =
        newSlots2[newSlots.indexOf(returnList[returnList.length - 1]) + 1]
      return `${firstSlot} TO ${lastSlot}`
    }
  }
