/* eslint-disable no-loop-func */
import React, { useEffect } from 'react'

import axios from 'axios'

import { withStyles, makeStyles } from '@material-ui/core/styles'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableContainer from '@material-ui/core/TableContainer'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Paper from '@material-ui/core/Paper'
import Button from '@material-ui/core/Button'
import Alert from '../../SnackbarAlert'

import backGroundHeader from './../../../images/backgroundCalendarHeader.png'

import { set } from 'date-fns'
import { useParams } from 'react-router'
import { backendLink } from '../../../keys_dev'
import { useSelector, useDispatch } from 'react-redux'
import { checkTokenExpired } from '../../../globalState/actions/AuthActions'
import { useHistory } from 'react-router'
import { Spinner } from 'react-bootstrap'

const StyledTableCell = withStyles((theme) => ({
  head: {
    height: '1vw',
    color: theme.palette.common.black,
    fontSize: ' 0.5vw',
    alignItems: 'bottom',
    verticalAlign: 'bottom',
    paddingTop: '2.8vw',
    paddingBottom: '0vw',
    display: 'inlineBlock',
  },

  body: {
    borderStyle: 'ridge',
    borderWidth: '1px',
    fontSize: ' 0.7vw',
  },
}))(TableCell)

const StyledTableRow = withStyles((theme) => ({
  root: {},
}))(TableRow)

const useStyles = makeStyles({
  table: {
    minWidth: 700,
    color: 'white',
  },
})

export default function CustomizedTables(props) {
  const dispatch = useDispatch()
  const history = useHistory()
  const [showAlert, setShowAlert] = React.useState(false)
  const [showAlertMess, setShowAlertMess] = React.useState('error')
  const [severity, setSeverity] = React.useState('error')
  const closealert = () => {
    setShowAlert(false)
  }
  const bookingId = useParams().bookingId

  const token = useSelector((state) => state.token)
  const id = useSelector((state) => state.id)
  const date = props.date
  console.log(date)
  const c = [
    {
      roomNumber: 1,
      filtered: false,
      notFreeSlots: [],
    },
    {
      roomNumber: 2,
      filtered: false,
      notFreeSlots: [
        {
          slotName: 'NINE_TEN',
          status: 'confirmed',
        },
        {
          slotName: 'TEN_ELEVEN',
          status: 'pending',
        },

        {
          slotName: 'TWELVE_THIRTEEN',
          status: 'confirmed',
        },

        {
          slotName: 'SEVENTEEN_EIGHTEEN',
          status: 'pending',
        },
        {
          slotName: 'EIGHTEEN_NINETEEN',
          status: 'pending',
        },
      ],
    },
    {
      roomNumber: 3,
      filtered: false,
      notFreeSlots: [
        {
          slotName: 'NINE_TEN',
          status: 'confirmed',
        },
        {
          slotName: 'TEN_ELEVEN',
          status: 'pending',
        },
        {
          slotName: 'ELEVEN_TWELVE',
          status: 'Free',
        },

        {
          slotName: 'FOURTEEN_FIFTEEN',
          status: 'confirmed',
        },
        {
          slotName: 'FIFTEEN_SIXTEEN',
          status: 'confirmed',
        },
        {
          slotName: 'SIXTEEN_SEVENTEEN',
          status: 'pending',
        },

        {
          slotName: 'NINETEEN_TWENTY',
          status: 'confirmed',
        },
        {
          slotName: 'TWENTY_TWENTYONE',
          status: 'pending',
        },
      ],
    },
    {
      roomNumber: 4,
      filtered: false,
      notFreeSlots: [
        {
          slotName: 'NINE_TEN',
          status: 'confirmed',
        },
        {
          slotName: 'TEN_ELEVEN',
          status: 'pending',
        },
        {
          slotName: 'ELEVEN_TWELVE',
          status: 'Free',
        },
        {
          slotName: 'TWELVE_THIRTEEN',
          status: 'confirmed',
        },
        {
          slotName: 'THIRTEEN_FOURTEEN',
          status: 'confirmed',
        },
        {
          slotName: 'FOURTEEN_FIFTEEN',
          status: 'confirmed',
        },
        {
          slotName: 'FIFTEEN_SIXTEEN',
          status: 'confirmed',
        },
        {
          slotName: 'SIXTEEN_SEVENTEEN',
          status: 'pending',
        },
        {
          slotName: 'SEVENTEEN_EIGHTEEN',
          status: 'confirmed',
        },
        {
          slotName: 'EIGHTEEN_NINETEEN',
          status: 'confirmed',
        },
        {
          slotName: 'NINETEEN_TWENTY',
          status: 'confirmed',
        },
        {
          slotName: 'TWENTY_TWENTYONE',
          status: 'pending',
        },
      ],
    },
  ]
  const classes = useStyles()
  const [calendar, setCalendar] = React.useState(c)
  const [rows, setRows] = React.useState([[], [], [], []])
  const [roomSelected, setRoomSelected] = React.useState('none')
  const [slotsSelected, setSlotsSelected] = React.useState(0)
  const [slotsNamesSelected, setSlotsNameSelected] = React.useState([])
  const [firstSlot, setFirstSlot] = React.useState(0)
  const [lastSlot, setLastSlot] = React.useState(0)
  const [loading, setLoading] = React.useState(true)

  useEffect(() => {
    if (!dispatch(checkTokenExpired(history))) {
      let newDate = new Date(date).toDateString()
      setLoading(true)
      axios({
        url: `${backendLink}/tbhapp/booking/viewCalendar`,
        method: 'POST',
        headers: {
          authorization: token,
        },
        data: {
          Account: { id },
          startDate: newDate,
        },
      }).then((res) => {
        console.log(res)
        setRows([[], [], [], []])
        if (res.data.statusCode === 0) {
          setCalendar(res.data.calendar)

          let rows1 = []
          let added = false
          let numberOfSlots = 0
          res.data.calendar.map((room) => {
            let row = []
            for (var i = 0; i < slots.length; i++) {
              let slott = slots[i]

              room.notFreeSlots.map((slot, index) => {
                if (slot.slotName === slott) {
                  if (`${slot.bookingId}` === bookingId) {
                    setRoomSelected(`room${room.roomNumber}`)
                    props.setRoomSelectedP(`room${room.roomNumber}`)
                    let v = slotsNamesSelected
                    v.push(slot.slotName)
                    setSlotsNameSelected(v)
                    props.setSlotsNameSelectedP(v)
                    console.log(numberOfSlots)
                    numberOfSlots += 1
                    console.log(numberOfSlots)
                    added = true
                    let slotsNamed = [
                      'NINE_TEN',
                      'TEN_ELEVEN',
                      'ELEVEN_TWELVE',
                      'TWELVE_THIRTEEN',
                      'THIRTEEN_FOURTEEN',
                      'FOURTEEN_FIFTEEN',
                      'FIFTEEN_SIXTEEN',
                      'SIXTEEN_SEVENTEEN',
                      'SEVENTEEN_EIGHTEEN',
                      'EIGHTEEN_NINETEEN',
                      'NINETEEN_TWENTY',
                      'TWENTY_TWENTYONE',
                    ]
                    if (index === 0) {
                      setFirstSlot(slotsNamed.indexOf(slot.slotName))
                    } else {
                      setLastSlot(slotsNamed.indexOf(slot.slotName))
                    }
                  }
                  return row.push([
                    slot.status,
                    slot.accountId,
                    `${slot.bookingId}`,
                  ])
                }
              })
              setSlotsSelected(numberOfSlots)
              if (row[i] === undefined) {
                row.push(['Free'])
              }
            }
            rows1.push(row)
          })
          setRows(rows1)

          if (!added) {
            setRoomSelected('none')
            props.setRoomSelectedP('none')
            setSlotsSelected(0)
            setSlotsNameSelected([])
            props.setSlotsNameSelectedP([])
          }
        }
        setLoading(false)
      })
    }
  }, [props.date])

  let slots = [
    'NINE_TEN',
    'TEN_ELEVEN',
    'ELEVEN_TWELVE',
    'TWELVE_THIRTEEN',
    'THIRTEEN_FOURTEEN',
    'FOURTEEN_FIFTEEN',
    'FIFTEEN_SIXTEEN',
    'SIXTEEN_SEVENTEEN',
    'SEVENTEEN_EIGHTEEN',
    'EIGHTEEN_NINETEEN',
    'NINETEEN_TWENTY',
    'TWENTY_TWENTYONE',
  ]

  const isItOkaySelect = (slotNumber) => {
    console.log('wow' + slotNumber)
    if (slotNumber === firstSlot - 1) {
      const x = firstSlot - 1
      setFirstSlot(x)
      return true
    }
    if (slotNumber === lastSlot + 1) {
      setLastSlot(lastSlot + 1)
      return true
    }
    return false
  }

  const isItOkayDeselect = (slotNumber) => {
    // if (slotNumber === firstSlot && slotNumber === lastSlot) {
    //   setFirstSlot(0)
    //   setLastSlot(0)
    //   return true
    // }
    if (slotNumber === firstSlot) {
      setFirstSlot(firstSlot + 1)
      return true
    }
    if (slotNumber === lastSlot) {
      setLastSlot(lastSlot - 1)
      return true
    }

    return false
  }

  const reply_click = (e) => {
    console.log(e.target.id)

    if (slotsSelected === 0) {
      setRoomSelected('none')
      props.setRoomSelectedP('none')
    }
    if (roomSelected === 'none') {
      if (e.target.style.backgroundColor === 'transparent') {
        e.target.style.backgroundColor = '#0087D2'
        setRoomSelected(e.target.id.substring(0, 5))
        props.setRoomSelectedP(e.target.id.substring(0, 5))
        setSlotsSelected(1)
        setFirstSlot(parseInt(e.target.id.substring(6)))
        setLastSlot(parseInt(e.target.id.substring(6)))
        setSlotsNameSelected([slots[parseInt(e.target.id.substring(6))]])
        props.setSlotsNameSelectedP([slots[parseInt(e.target.id.substring(6))]])
      } else {
        e.target.style.backgroundColor = 'transparent'
      }
    } else {
      if (e.target.style.backgroundColor === 'transparent') {
        if (e.target.id.substring(0, 5) === roomSelected) {
          if (isItOkaySelect(parseInt(e.target.id.substring(6))) === false) {
            setShowAlert(true)
            setSeverity('error')
            setShowAlertMess('Sorry! You can only select consecutive slots')
          } else {
            e.target.style.backgroundColor = '#0087D2'
            setSlotsSelected(slotsSelected + 1)
            let v = slotsNamesSelected
            v[slotsSelected] = slots[parseInt(e.target.id.substring(6))]
            setSlotsNameSelected(v)
            props.setSlotsNameSelectedP(v)
          }
        } else {
          console.log('error')
        }
      } else {
        if (isItOkayDeselect(parseInt(e.target.id.substring(6))) === false) {
          setShowAlert(true)
          setSeverity('error')

          setShowAlertMess('Sorry! You can only select consecutive slots')
          console.log("CAN'T")
        } else {
          e.target.style.backgroundColor = 'transparent'
          if (slotsSelected - 1 === 0) {
            setRoomSelected('none')
            props.setRoomSelectedP('none')
          }
          setSlotsSelected(slotsSelected - 1)
          let v = slotsNamesSelected
          console.log(v)
          const x = v.indexOf(slots[parseInt(e.target.id.substring(6))])
          v.splice(x, 1)
          setSlotsNameSelected(v)
          props.setSlotsNameSelectedP(v)
        }
      }
    }
  }

  return (
    <div>
      {loading ? (
        <div
          style={{
            position: 'fixed',
            width: '100%',
            height: '100%',
            top: '0',
            left: '0',
            right: '0',
            bottom: '0',
            backgroundColor: 'rgba(0,0,0,0.5)',
            zIndex: '2',
            display: 'block',
          }}
        >
          <div
            style={{
              position: 'absolute',
              top: '50%',
              left: '50%',
              color: 'white',
              transform: 'translate(-50%,-50%)',
              width: '30vw',
              marginRight: '0px',
              display: 'flex',
              alignContent: 'center',
              justifyContent: 'center',
              width: '10vw',
              height: '10vw',
            }}
          >
            <Spinner animation='border' role='status'>
              <span className='sr-only'>Loading...</span>
            </Spinner>
          </div>
        </div>
      ) : null}
      <Alert
        showAlert={showAlert}
        handleClose={closealert}
        severity={severity}
        msg={showAlertMess}
      />
      <TableContainer
        component={Paper}
        style={{
          backgroundImage: `url(${backGroundHeader})`,
          backgroundSize: '100% 23%',
          backgroundRepeat: 'no-repeat',
          width: '100%',
        }}
      >
        <Table className={classes.table} aria-label='customized table'>
          <TableHead>
            <TableRow>
              <StyledTableCell></StyledTableCell>
              <StyledTableCell
                style={{ marginLeft: '100vw', color: 'white' }}
                align='right'
              >
                9:00&nbsp;AM
              </StyledTableCell>
              <StyledTableCell align='center' style={{ color: 'white' }}>
                10:00&nbsp;AM
              </StyledTableCell>
              <StyledTableCell align='center' style={{ color: 'white' }}>
                11:00&nbsp;AM
              </StyledTableCell>
              <StyledTableCell align='center' style={{ color: 'white' }}>
                12:00&nbsp;PM
              </StyledTableCell>
              <StyledTableCell align='center' style={{ color: 'white' }}>
                1:00&nbsp;PM
              </StyledTableCell>
              <StyledTableCell align='center' style={{ color: 'white' }}>
                2:00&nbsp;PM
              </StyledTableCell>
              <StyledTableCell align='center' style={{ color: 'white' }}>
                3:00&nbsp;PM
              </StyledTableCell>
              <StyledTableCell align='center' style={{ color: 'white' }}>
                4:00&nbsp;PM
              </StyledTableCell>
              <StyledTableCell align='center' style={{ color: 'white' }}>
                5:00&nbsp;PM
              </StyledTableCell>
              <StyledTableCell align='center' style={{ color: 'white' }}>
                6:00&nbsp;PM
              </StyledTableCell>
              <StyledTableCell align='center' style={{ color: 'white' }}>
                7:00&nbsp;PM
              </StyledTableCell>
              <StyledTableCell align='center' style={{ color: 'white' }}>
                8:00&nbsp;PM
              </StyledTableCell>
            </TableRow>
          </TableHead>
          <TableBody style={{ backgroundColor: 'white' }}>
            {console.log('toot', new Date(date), new Date())}
            {calendar.map((room, index) => (
              <StyledTableRow key={room.roomNumber}>
                <StyledTableCell
                  style={{ fontWeight: 'bold', fontSize: '0.7vw' }}
                  component='th'
                  scope='row'
                >
                  ROOM{room.roomNumber}
                </StyledTableCell>

                {rows[index].map((row, index) => {
                  if (
                    index + 9 <= new Date().getHours() &&
                    new Date(date) < new Date()
                  ) {
                    return (
                      <StyledTableCell
                        style={{
                          backgroundColor: 'white',
                          width: '4vw',
                          height: '3.5vw',
                          padding: '0',
                        }}
                        align='center'
                      >
                        Unavailable
                      </StyledTableCell>
                    )
                  } else {
                    if (row[0] === 'Free') {
                      return (
                        <StyledTableCell
                          align='center'
                          style={{ padding: '0' }}
                        >
                          <Button
                            id={'room' + room.roomNumber + '/' + index}
                            onClick={(e) => {
                              reply_click(e)
                              // slotsify(index)
                            }}
                            style={{
                              borderRadius: '0',
                              backgroundColor: 'transparent',
                              border: 'none',
                              height: '3.5vw',
                              width: '4vw',
                              padding: '0',
                            }}
                            type='submit'
                          ></Button>
                        </StyledTableCell>
                      )
                    }
                    if (row[0] === 'pending') {
                      if (row[1] === id) {
                        if (row[2] === bookingId) {
                          return (
                            <StyledTableCell
                              align='center'
                              style={{
                                padding: '0',
                                width: '4vw',
                                height: '3.5vw',
                              }}
                            >
                              <Button
                                id={'room' + room.roomNumber + '/' + index}
                                onClick={(e) => {
                                  reply_click(e)
                                  // slotsify(index)
                                }}
                                color='#0EBDB3'
                                style={{
                                  borderRadius: '0',
                                  backgroundColor: '#0EBDB3',
                                  border: 'none',
                                  height: '3.5vw',
                                  width: '4vw',
                                }}
                                type='submit'
                              ></Button>
                            </StyledTableCell>
                          )
                        } else {
                          return (
                            <StyledTableCell
                              style={{
                                backgroundColor: '#606060',
                                width: '4vw',
                                height: '3.5vw',
                                padding: '0',
                              }}
                              align='center'
                            >
                              Your {row[0]}
                            </StyledTableCell>
                          )
                        }
                      } else
                        return (
                          <StyledTableCell
                            style={{
                              backgroundColor: '#C4C4C4',
                              width: '4vw',
                              height: '3.5vw',
                              padding: '0',
                            }}
                            align='center'
                          >
                            {row[0]}
                          </StyledTableCell>
                        )
                    }
                    if (row[0] === 'confirmed') {
                      return (
                        <StyledTableCell
                          style={{
                            backgroundColor: '#ED1C24',
                            width: '4vw',
                            height: '3.5vw',
                            padding: '0',
                          }}
                          align='center'
                        >
                          {row[0]}
                        </StyledTableCell>
                      )
                    }
                  }
                })}
              </StyledTableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>{' '}
    </div>
  )
}
