import React from 'react'
import 'bootstrap/dist/css/bootstrap.css'
import 'react-phone-input-2/lib/style.css'
import '../../App.css'
import WelcomeSignIn from './SignIn'
import WelcomeSignUp from './SignUp'

export default function signInUp(props) {
  return (
    <>
      <MyVerticallyCenteredModal sideNav = {props.sideNav}/>
    </>
  )
}

function MyVerticallyCenteredModal(props) {
  const [signIn, setSignIn] = React.useState(false)
  const [signUp, setSignUp] = React.useState(false)
  const handleSignin = (value) => {
    if (value === true) setSignUp(false)
    setSignIn(value)
  }
  const handleSignup = (value) => {
    if (value === true) setSignIn(false)
    setSignUp(value)
  }
  const signInUp = () => {
    if (signIn) {
      return (
        <WelcomeSignIn
          handleSignIn={handleSignin}
          handleSignUp={handleSignup}
        />
      )
    }
    if (signUp) {
      return (
        <div>
          <WelcomeSignUp
            handleSignUp={handleSignup}
            handleSignIn={handleSignin}
          />
        </div>
      )
    }
  }
  return (
    <div>
      <div
        style = {props.sideNav ? {
          width: '20vw',
                   display: 'flex' , 
                   flex : 'row',
                    marginTop : '1vw',
                    // color: pathname === '/book/-' ? '#D20008' : 'black',
                    // borderColor: pathname === '/book/-' ? '#D20008' : 'black',
                    background : 'none',
                    border : 'none',
                    fontSize: '1.5vw',
                    // overflow: 'hidden',
                    // textOverflow: 'ellipsis',
                    marginLeft: '-3vw',
                    fontWeight: 'bold',
                    color : '#ffffff'
        } : { display: 'flex',
        flexDirection: 'row',
        marginTop: '0.2vw',
        fontSize: '1vw',
        color: '#D20008',
        cursor: 'pointer',
        textAlign: 'left',}}
      >
        <div onClick={() => setSignIn(true)}>SIGN</div> &nbsp;
        <div onClick={() => setSignIn(true)}>IN</div> &nbsp;
        <div>/</div> &nbsp;
        <div onClick={() => setSignUp(true)}> REGISTER </div>
      </div>
      {signInUp()}
    </div>
  )
}
