import axios from 'axios'
import jwt from 'jsonwebtoken'
import { backendLink } from '../../keys_dev'
export const loginAPI = (username, password, history, handleAlert) => {
  return async (dispatch, getState) => {
    return await axios({
      method: 'post',
      url: `${backendLink}/tbhapp/accounts/login`,
      data: {
        Account: { username, password },
      },
    }).then((res) => {
      if (res.data.statusCode === 0) {
        dispatch(setToken(res.data.token))
        dispatch(setID(res.data.id))
        dispatch(setName(res.data.username))
        dispatch(setVerified(res.data.state))
        history.push('/home')
        return 0
      } else {
        if (res.data.statusCode === 104) {
          dispatch(unsetToken())
          return 104
        }
      }
    })
  }
}
export const lirtenLoginAPI = (
  token,
  id,
  username,
  state,
  history,
  handleAlert
) => {
  return async (dispatch, getState) => {
    dispatch(setToken(token))
    dispatch(setID(id))
    dispatch(setName(username))
    dispatch(setVerified(state))
    history.push('/home')
    return 0
  }
}

export const resetToken = (token) => {
  return async (dispatch, getState) => {
    return await axios({
      method: 'post',
      url: `${backendLink}/tbhapp/accounts/resendToken`,
      data: {
        Account: {
          id: getState().id,
        },
        token,
      },
      headers: {
        authorization: getState().token,
      },
    }).then((res) => {
      console.log(res)
      if (res.data.statusCode === 0) {
        dispatch(setToken(res.data.token))
        dispatch(setID(res.data.id))
        dispatch(setName(res.data.username))
        dispatch(setVerified(res.data.state))
        return 0
      }
    })
  }
}

