export const registerGoogle = (state, history, handleAlert) => {
  return async (dispatch, getState) => {
    return await axios({
      method: 'post',
      url: `${backendLink}/tbhapp/accounts/registergoogle`,
      data: {
        Account: {
          username: state.username,
          firstName: state.firstName,
          lastName: state.lastName,
          phoneNumber: state.phoneNumber,
          id: state.id,
          email: state.email,
        },
      },
    }).then(async (res) => {
      if (res.data.statusCode === 0) {
        const x = await dispatch(loginGoogle(state.id, history))
        history.push('/home')
        return 0
      } else {
        dispatch(unsetToken())
        return res.data.statusCode
      }
    })
  }
}

