export const logout = (history) => {
  return (dispatch, getState) => {
    dispatch(unsetToken(''))
    dispatch(unsetID(''))
    dispatch(unsetName())
    dispatch(unsetVerified(false))
    history.push('/home/SignIn')
  }
}
