import React, { useState, useEffect } from 'react'
import Navbar from 'react-bootstrap/Navbar'
import Nav from 'react-bootstrap/Nav'
// import Button from 'react-bootstrap/Button'
import { Button } from '@material-ui/core'

import Logo from '../images/TBHLogoHeader.png'
import { Avatar, Typography } from '@material-ui/core'
import { useSelector, useDispatch } from 'react-redux'
import SignInUp from './Authentication/SignInUpButton'
import { logout } from '../globalState/actions/AuthActions'
import { useHistory, useLocation } from 'react-router-dom'
import verify from '../images/verify.png'
import axios from 'axios'
import VerifyModal from './VerificationModal'
import HeaderSettings from './HeaderSettings'

export default function Header() {
  const nav = React.createRef()
  const { pathname } = useLocation()
  const dispatch = useDispatch()
  const [show, setShow] = useState(true)
  const history = useHistory()
  const token = useSelector((state) => state.token)
  const name = useSelector((state) => state.name)
  const verifiedState = useSelector((state) => state.verified)
  const [menu, setMenu] = useState(false)
  const [rerender, setRerender] = useState(false)
  const [page, setPage] = useState({
    home: true,
    booking: false,
    packages: false,
  })
  const [verified, setVerified] = React.useState(
    verifiedState !== '' && verifiedState !== 'verified'
  )
  const [windowWidth, setWindowWidth] = React.useState(window.innerWidth)
  const id = useSelector((state) => state.id)
  const [verifyModalOpen, setVerifyModalOpen] = React.useState(false)

  useEffect(() => {
    setMenu(false)
    setVerified(verifiedState !== '' && verifiedState !== 'verified')
    window.addEventListener('resize', () => setWindowWidth(window.innerWidth))
    if (pathname.includes('/book')) {
      setPage({ home: false, booking: true, packages: false })
    } else if (pathname.includes('/Packages')) {
      setPage({ home: false, booking: false, packages: true })
    }
  }, [verifiedState, pathname, rerender])

  const handleClose = () => {
    setVerifyModalOpen(false)
  }
  let pleaseVerifyComponent = []
  if (verified) {
    pleaseVerifyComponent.push(
      <tr
        style={{
          backgroundColor: '#ED1C24',
          width: '100%',
          display: 'flex',
          justifyContent: 'center',
          height: '2vw',
          alignItems: 'center',
          cursor: 'pointer',
        }}
        onClick={() => setVerifyModalOpen(true)}
      >
        <Typography
          style={{ color: 'white', fontSize: '1vw', fontWeight: '500' }}
        >
          <img
            src={verify}
            style={{
              color: 'white',
              width: '1vw',
              marginRight: '1vw',
            }}
          />
          Please verify your phone number !
        </Typography>
      </tr>
    )
  }
  const NavBar = React.forwardRef((props, ref) => {
    return (
      <Navbar bg='white' expand='lg' ref={ref}>
        <Navbar.Brand
          href='/'
          style={{
            marginLeft: '10vw',
            marginRight: '10vw',
          }}
        >
          <img src={Logo} alt='' style={{ width: '8.3vw', height: '5vw' }} />
        </Navbar.Brand>
        <Navbar.Toggle aria-controls='basic-navbar-nav' />
        <Navbar.Collapse id='basic-navbar-nav'>
          <div
            style={{
              display: 'flex',
              flexDirection: 'column',
            }}
          >
            <Nav
              className='mr-auto'
              style={{
                display: 'flex',
                height: '2vw',
                marginBottom: '1vw',
                flexDirection: 'row',
                justifyContent: 'center',
                alignItems: 'center',
                width: '100%',
                marginTop: '1vw',
              }}
            >
              <Button
                onClick={() => {
                  history.push('/home')
                  setPage({ packages: false, home: true, booking: false })
                }}
                style={{
                  marginLeft: '-6vw',
                  color: page['home'] === true ? '#ED1C24' : 'black',
                  fontSize: '0.9vw',
                  fontWeight: 'bold',
                  outline: '0',
                  border: '0',
                  '&:focus': { border: '0', outline: '0' },
                }}
              >
                HOME
              </Button>
              <Button
                onClick={() => {
                  history.push('/book/-')
                  setPage({ packages: false, home: false, booking: true })
                }}
                style={{
                  marginLeft: '2vw',
                  color: page['booking'] === true ? '#ED1C24' : 'black',
                  fontSize: '0.9vw',
                  fontWeight: 'bold',

                  outline: '0',
                  border: '0',
                  '&:focus': { border: '0', outline: '0' },
                }}
              >
                BOOKING
              </Button>
              <Button
                onClick={() => {
                  history.push('/Packages')
                  setPage({ packages: true, home: false, booking: false })
                }}
                style={{
                  marginLeft: '2vw',
                  fontSize: '0.9vw',
                  color: page['packages'] === true ? '#ED1C24' : 'black',
                  fontWeight: 'bold',
                  outline: '0',
                  border: '0',
                  '&:focus': { border: '0', outline: '0' },

                  backgroundColor: 'white',

                  marginRight: '22.2vw',
                }}
              >
                {' '}
                PACKAGES
              </Button>
              {/* <Nav.Link
                href="/Packages"
                style={{
                  marginLeft: '2vw',
                  fontSize: '0.9vw',
                  color: pathname === '/Packages' ? '#282828' : 'black',
                  fontWeight: 'bold',
                  
                  marginRight: '14.2vw',

                }}
              >
                PACKAGES
              </Nav.Link> */}
              {/* <Nav.Link
                style={{
                  marginLeft: '2vw',
                  color: 'black',
                  fontSize: '0.9vw',
                  fontWeight: 'bold',
                  
                  overflow: 'hidden',
                  textOverflow: 'ellipsis',
                  width: '13vw',
                }}
              >
                SERVICES & PACKAGES
              </Nav.Link> */}
              {/* <Nav.Link
                href="/Courses"
                style={{
                  color: pathname === '/Courses' ? '#282828 ' : 'black',
                  fontSize: '0.9vw',
                  fontWeight: 'bold',
                  
                  overflow: 'hidden',
                  textOverflow: 'ellipsis',
                  marginRight: '14.2vw',
                  marginLeft: '2vw',
                }}
              >
                COURSES
              </Nav.Link> */}
              <Button
                onClick={() => {
                  history.push('/settings/Organize')
                  setPage({ home: false, booking: false, packages: false })
                }}
                style={{
                  width: '13vw',
                  color: 'white',
                  borderColor: pathname.includes('/Organize')
                    ? 'black'
                    : '#D20008',
                  backgroundColor: pathname.includes('/Organize')
                    ? 'black'
                    : '#D20008',
                  marginRight: '1vw',
                  marginLeft: '2vw',
                  height: '2vw',
                  fontSize: '0.94vw',
                  display: 'flex',
                  justifyContent: 'center',
                  alignItems: 'center',
                  fontWeight: '500',

                  borderRadius: '0px',
                }}
              >
                ORGANIZE WITH US
              </Button>
              <div
                style={{
                  backgroundColor: '#D20008',
                  border: 'none',
                  width: '0.2vw',
                  marginTop: '0.2vw',
                  height: '1.5vw',
                }}
              ></div>
              <Nav.Link
                onClick={() => {
                  history.push('/book/-')
                  setPage({ packages: false, home: false, booking: true })
                }}
                style={{
                  width: '9vw',
                  marginLeft: '1vw',
                  fontSize: '0.9vw',
                  overflow: 'hidden',
                  textOverflow: 'ellipsis',
                  marginRight: '-2.5vw',
                  fontWeight: 'bold',

                  color: 'black',
                }}
              >
                BOOK NOW
              </Nav.Link>

              {token !== '' ? (
                <div
                  onClick={() => {
                    setMenu(!menu)
                  }}
                >
                  <Avatar
                    style={{
                      color: 'white',
                      backgroundColor: '#D20008',
                      marginLeft: '2vw',
                      cursor: 'pointer',
                      width: '2vw',
                      height: '2vw',
                      fontSize: '0.9vw',

                      fontWeight: '500',
                    }}
                  >
                    {name.substring(0, 1).toUpperCase()}
                  </Avatar>
                  <HeaderSettings
                    show={menu}
                    setShow={setMenu}
                    click={(page) => {
                      setMenu(false)
                      setRerender(!rerender)
                      history.push(`${page}`, { update: true })
                    }}
                  />
                </div>
              ) : (
                <div style={{ marginTop: '0.2vw', marginLeft: '2vw' }}>
                  <SignInUp />
                </div>
              )}
            </Nav>

            {
              // windowWidth > 991 ? (
              //   <div
              //     style={{
              //       width: '65vw',
              //       backgroundColor: '#D20008',
              //       height: '0.05vw',
              //       marginLeft: '-8vw',
              //     }
              //   }
              //   ></div>
              // ) : null
            }
          </div>
        </Navbar.Collapse>
      </Navbar>
    )
  })

  return (
    <div
      style={{
        //   border: '1px solid #EC2227',
        boxSizing: 'border-box',
        boxShadow: '0px 0px 8px rgba(0, 0, 0, 0.45)',
      }}
    >
      {console.log(menu)}
      <NavBar ref={nav} />
      {pleaseVerifyComponent}
      {verifyModalOpen ? <VerifyModal handleClose={handleClose} /> : null}
    </div>
  )
}
