export default function Gift(props) {
  const dispatch = useDispatch()
  const history = useHistory()
  const [showAlert, setShowAlert] = React.useState(false)
  const [showAlertMess, setShowAlertMess] = React.useState('error')
  const [severity, setSeverity] = React.useState('error')
  const closealert = () => {
    setShowAlert(false)
  }
  const handleClose = () => {
    props.setgiftModal(false)
  }
  const handleAlert = () => {
    setShowAlert(true)
    setShowAlertMess('Gift points are sent !')
    setSeverity('success')
  }
  const token = useSelector((state) => state.token)
  const id = useSelector((state) => state.id)
  const [email, setEmail] = React.useState('')
  const handleChangeEmail = (e) => {
    setEmail(e.target.value)
  }
  const [points, setPoints] = React.useState('')
  const handleChangePoints = (e) => {
    setPoints(e.target.value)
  }
  const [phoneNumber, setPhoneNumber] = React.useState('')
  const handleChangePhoneNumber = (e) => {
    setPhoneNumber(e.target.value)
  }
  const [validationEmailDisplay, setValidationEmailDisplay] = React.useState(
    'none'
  )

  const [
    validationPhonenumberDisplay,
    setValidationPhonenumberDisplay,
  ] = React.useState('none')

  const [validationEmail, setValidationEmail] = React.useState('')

  const [validationPhonenumber, setValidationPhonenumber] = React.useState('')
  const [validated, setValidated] = React.useState(false)
  const [state, setState] = React.useState({})
  const classes = useStyles()
  const phoneValidation = (form) => {
    if (state.phoneNumber.length !== 11) {
      setValidationPhonenumber('* phonenumber must be 11 characters long')
      setValidationPhonenumberDisplay('block')
      return false
    }
    setValidationPhonenumber('')
    setValidationPhonenumberDisplay('none')
    return true
  }
  const send = async (event) => {
    event.preventDefault()
    const form = event.currentTarget
    if (form.checkValidity() === false) {
      setValidationEmail('')

      setValidationPhonenumber('')
      setValidationEmailDisplay('none')

      setValidationPhonenumberDisplay('none')
      event.preventDefault()
      event.stopPropagation()
    } else {
      console.log(id)
      if (!dispatch(checkTokenExpired(history))) {
        axios({
          url: `${backendLink}/tbhapp/package/sendGift`,
          method: 'POST',
          headers: {
            authorization: token,
          },
          data: {
            Account: { id: `${id}` },
            email: email,
            points: points,
          },
        })
          .then((res) => {
            console.log(res)
            if (res.data.statusCode === 0) {
              setTimeout(() => {
                setShowAlert(true)
                setShowAlertMess('Gift points sent successfuly')
                setSeverity('success')
              }, 5000)
              props.setgiftModal(false)
            } else {
              if (res.data.statusCode === 139) {
                setShowAlert(true)
                setShowAlertMess('Not enough points!')
                setSeverity('error')
              }
            }
          })
          .catch((error) => {
            console.log(error)
          })
      }
    }
    setValidated(true)
  }

  const Dialog = () => {
    return (
      <div>
        <Alert
          showAlert={showAlert}
          handleClose={closealert}
          severity={severity}
          msg={showAlertMess}
        />
        <Form
          style={{ marginLeft: '12vw', height: '20vw' }}
          noValidate
          validated={validated}
          onSubmit={send}
        >
          <Row>
            <text
              style={{
                marginTop: '-8vw',
                fontSize: '1.2vw',
                color: '#D20008',
                fontWeight: 'bolder',
                marginLeft: '9.2vw',
              }}
            >
              GIFTING A FRIEND !
            </text>
          </Row>
          <Row>
            <text
              style={{
                marginTop: '-5vw',
                fontSize: '1vw',
                color: 'black',
                fontWeight: 'bold',
                marginLeft: '5vw',
              }}
            >
              To send gifted points, please verify the following
              <text style={{ marginLeft: '9vw' }}> information</text>
            </text>
          </Row>
          <Form.Control
            className={classes.formControl}
            name='email'
            value={email}
            onChange={handleChangeEmail}
            required
            type='email'
            placeholder='E-Mail'
          ></Form.Control>
          <Row>
            <text
              className={classes.errorValidation}
              style={{ display: validationEmailDisplay }}
            >
              {validationEmail}
            </text>
          </Row>
          <Form.Control
            className={classes.formControl}
            name='phoneNumber'
            value={phoneNumber}
            onChange={handleChangePhoneNumber}
            required
            type='Number'
            placeholder='Phone Number (01*********)'
          ></Form.Control>
          <Row>
            <text
              className={classes.errorValidation}
              style={{ display: validationPhonenumberDisplay }}
            >
              {validationPhonenumber}
            </text>
          </Row>
          <Form.Control
            className={classes.formControl}
            name='points'
            onChange={handleChangePoints}
            required
            type='Number'
            placeholder='Amount Of Points'
          ></Form.Control>
          <Button
            style={{
              marginLeft: '10vw',
              marginTop: '2vw',
              backgroundColor: '#D20008',
              border: 'none',
              cursor: 'pointer',
              fontSize: '1vw',
            }}
            type='submit'
          >
            SEND
          </Button>
        </Form>
      </div>
    )
  }

  return (
    <div>
      <div>
        <div id='outerDiv' className={classes.overlay}>
          <div
            style={{
              position: 'absolute',
              top: '50%',
              left: '50%',
              backgroundColor: 'white',
              color: 'white',
              transform: 'translate(-50%,-50%)',
              width: '70.93vw',
              border: '0.1vw solid #ec2227',
              borderRadius: '0.5vw 0px 0px 0.5vw',
              marginRight: '0px',
            }}
          >
            <Table
              style={{
                height: '100%',
                marginBottom: '0px',
                marginRight: '0px',
                width: '100%',
                maxWidth: 'unset',
                padding: '0px',
              }}
              borderless
            >
              <tbody
                style={{ height: '100%', marginRight: '0px', padding: '0px' }}
              >
                <td
                  style={{
                    backgroundColor: '#EC2227',
                    width: '1.5vw',
                    border: '0.06vw solid #EC2227',
                    paddingLeft: '0px',
                    paddingRight: '0px',
                    marginRight: '0px',
                    padding: '0px',
                  }}
                ></td>

                <td style={{ padding: '0', borderTop: '0' }}>
                  <td>
                    <img
                      style={{ height: '8vw', width: '8vw', marginLeft: '2vw' }}
                      src={upperRight}
                    ></img>
                  </td>
                  <Table style={{ maxWidth: 'unset', border: 'none' }}>
                    <tbody>
                      <tr className={classes.td}>
                        <tr>
                          <p> </p>
                        </tr>
                        <tr>
                          <td className={classes.leftCol}>
                            <tr>{Dialog()}</tr>
                          </td>
                        </tr>
                      </tr>
                    </tbody>
                  </Table>
                </td>
                <td>
                  <tr>
                    <Row>
                      <img
                        src={xsign}
                        className={classes.xsignStyle}
                        alt='exit'
                        onClick={() => handleClose()}
                      />
                    </Row>
                  </tr>
                  <img
                    style={{
                      marginLeft: 'vw',
                      height: '20vw',
                      width: '25vw',
                      marginTop: '7vw',
                    }}
                    src={cash}
                  ></img>
                </td>

                <tr></tr>
              </tbody>
            </Table>
          </div>
        </div>
      </div>
    </div>
  )
}
