import React, { useState } from 'react'
import DatePicker from 'react-datepicker'
import 'react-datepicker/dist/react-datepicker.css'
import '../../../datepicker.css'
import backGroundHeader from './../../../images/backgroundCalendarHeader.png'
import { makeStyles } from '@material-ui/core/styles'
import { getDay, subDays } from 'date-fns'

const useStyles = makeStyles({
  root: {
    color: 'red',
    background: '#E2E2E2',
    boxShadow: '0.2vw 0.2vw 0.6vw rgba(0, 0, 0, 0.1)',
    borderRadius: '1.12vw',
    width: '76.8vw',
    marginLeft: 'auto',
    marginRight: 'auto',
    padding: '0',
    paddingTop: '1%',
  },
})

export default function DateP(props) {
  const [startDate, setStartDate] = useState(
    new Date(new Date().setDate(new Date().getDate())).getDay() !== 5
      ? new Date().setDate(new Date().getDate())
      : new Date().setDate(new Date().getDate() + 1)
  )
  console.log(new Date(startDate))
  const classes = useStyles()

  const onChange = (date) => {
    setStartDate(null)
    setStartDate(date)
    props.setDateP(date)
  }
  console.log(props)
  const notFriday = (date) => {
    const day = getDay(date)
    return day !== 5
  }

  return (
    <DatePicker
      className={classes.root}
      highlightDates={false}
      minDate={
        new Date(new Date().setDate(new Date().getDate())).getDay() !== 5
          ? new Date().setDate(new Date().getDate())
          : new Date().setDate(new Date().getDate() + 1)
      }
      selected={startDate}
      onChange={(date) => onChange(date)}
      adjustDateOnChange
      startDate={false}
      inline
      filterDate={notFriday}
      // excludeDates={[new Date(), subDays(new Date(),1)]}
    >
      {console.log(
        new Date(new Date().setDate(new Date().getDate() + 1)).getDay()
      )}
    </DatePicker>
  )
}
