  function handleSlotsNameChange() {
    const newSlots = [
      ' [9  10]',
      ' [10  11]',
      ' [11  12]',
      ' [12  13]',
      ' [13  14]',
      ' [14  15]',
      ' [15  16]',
      ' [16  17]',
      ' [17  18]',
      ' [18  19]',
      ' [19  20]',
      ' [20  21]',
    ]
    let oldSlots = [...slotsNamesSelected]
    console.log(slotsNamesSelected)

    let slots = [
      'NINE_TEN',
      'TEN_ELEVEN',
      'ELEVEN_TWELVE',
      'TWELVE_THIRTEEN',
      'THIRTEEN_FOURTEEN',
      'FOURTEEN_FIFTEEN',
      'FIFTEEN_SIXTEEN',
      'SIXTEEN_SEVENTEEN',
      'SEVENTEEN_EIGHTEEN',
      'EIGHTEEN_NINETEEN',
      'NINETEEN_TWENTY',
      'TWENTY_TWENTYONE',
    ]
    let returnList = []
    oldSlots.map((slot) => {
      returnList.push(newSlots[slots.indexOf(slot)])
    })
    return returnList.sort()
  }
  const [date, setDate] = React.useState(
    new Date().setDate(new Date().getDate() + 1)
  )
  const [roomSelected, setRoomSelected] = React.useState('none')

  const [doneRed, setDoneRed] = React.useState('#7E7E7E')

  const [openClose2, setOpenClose2] = React.useState(false)
