  const [slotsNamesSelected, setSlotsNameSelected] = React.useState([])
  const [showGroupSize, setGroupSize] = React.useState({
    small: 'true',
    large: 'false',
  })
  const [extremeType, setExtremeType] = React.useState('')
  const handleSetSlotsSelected = (x) => {
    const temp = [...x]
    setSlotsNameSelected(temp)
  }

  const [showPaymentMethod, setPaymentMethod] = React.useState({
    cash: 'false',
    points: 'false',
  })

