import React, { useState, useEffect } from 'react'
import Form from '../Forms/Form.js'

import CourseForm from '../Courses/CourseForm.js'
import AddedCourses from '../Courses/AddedCourses.js'

import axios from 'axios'
import { useSelector, useDispatch } from 'react-redux'
import { checkTokenExpired } from '../../globalState/actions/AuthActions'
import { useHistory } from 'react-router'

import Spinner from 'react-bootstrap/Spinner'
import { backendLink } from '../../keys_dev'
export default function OrganiserHomePage(props) {
  const dispatch = useDispatch()
  const history = useHistory()
  const token = useSelector((state) => state.token)
  const id = useSelector((state) => state.id)
  const [verified, setVerified] = useState(false)
  const [flag, setFlag] = useState(true)
  const [loading, setLoading] = useState(true)
  const [data, setData] = useState({})
  console.log(token)
  useEffect(() => {
    if (!dispatch(checkTokenExpired(history))) {
      axios({
        url: `${backendLink}/tbhapp/Form/viewform`,
        method: 'post',
        headers: { authorization: token },
        data: {
          Account: {
            id,
          },
          accountId: id,
        },
      }).then((res) => {
        console.log(res)
        if (res.data.statusCode === 136) {
          history.push('/Organize/createForm')
        } else {
          if (res.data.statusCode === 0) {
            console.log(res.data)
            setLoading(false)
            setData({})
          } else if (res.data.code === 105) {
            setLoading(false)
            setVerified(true)
          }
        }
      })
    }
  }, [])

  return (
    <div style={{ overflowX: 'hidden' }}>
      {loading ? (
        <div
          style={{
            fontSize: '1vw',
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            textAlign: 'center',
          }}
        >
          <p style={{ fontSize: '2vw' }}> Please Wait...</p> &nbsp; &nbsp;
          <Spinner animation="border" role="status">
            <span className="sr-only">Loading...</span>
          </Spinner>
        </div>
      ) : verified ? (
        <div
          style={{
            fontSize: '1vw',
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            textAlign: 'center',
          }}
        >
          <p style={{ fontSize: '2vw' }}> Please verify your phone number</p>{' '}
          &nbsp; &nbsp;
        </div>
      ) : (
        <div>
          <table style={{ marginLeft: '-2vw', width: '75vw' }}>
            <tr
              style={{
                fontSize: '1.2vw',
              }}
            >
              <p
                style={{
                  marginLeft: '28vw',
                  color: '#D20008',
                  fontWeight: 'bold',
                }}
              >
                HI
                <span style={{ color: 'black', fontWeight: 'normal' }}>
                  ,Your Organizing is all set Here.
                </span>
              </p>
            </tr>
            <tr>
              <div style={{ marginLeft: '5vw' }}>
                <Form
                  degree={'Bachelor Degree'}
                  university={'German University in Cairo'}
                  year={'2018'}
                  englishLevel={'Beginner'}
                  practiceCourse={'None'}
                  place={'guc'}
                  instruction={'I am a math instructor'}
                  audience={'I have a small following group'}
                  flag={flag}
                ></Form>
              </div>
            </tr>
            <tr>
              <div style={{ marginTop: '1vw' }}>
                <AddedCourses flag={flag} />
              </div>
            </tr>

            <tr>
              <div
                style={{
                  border: 'solid',
                  borderColor: '#D1D1D1',
                  margin: '10vw',
                  verticalAlign: 'middle',
                  marginTop: '0vw',
                  marginLeft: '5vw',
                  width: '50vw',
                }}
              >
                <CourseForm
                  flagt={true}
                  setFlag={(f) => setFlag(f)}
                ></CourseForm>
              </div>
            </tr>
          </table>
        </div>
      )}
    </div>
  )
}
