import React, { useEffect, useState } from 'react'
import { Avatar, Typography } from '@material-ui/core'
import BG from '../images/SidebarBackground.png'
import AccountSettings from './accounts/component/AccountSettings'
import ProfileSettings from './accounts/component/ProfileSettings'
import OrganizePage from './pages/OrganiserHomePage'
import BookingsPage from './Booking/Views/MyBookings'
import MyPackages from './Packages/MyPackages'
import axios from 'axios'
import { useParams, useHistory, useLocation } from 'react-router'
import verify from '../images/redVerify.png'
import { backendLink } from '../keys_dev'

import { useSelector, useDispatch } from 'react-redux'
import {
  checkTokenExpired,
  setEmailRedux,
} from '../globalState/actions/AuthActions'
export default function Sidebar(props) {
  const dispatch = useDispatch()
  const history = useHistory()
  const location = useParams().setting
  let { pathname } = useLocation()
  const [username, setUsername] = React.useState('')
  const [email, setEmail] = React.useState('')
  const [Account, setAccount] = React.useState(
    location === 'Account' ||
      pathname === '/Account/facebook' ||
      pathname === '/Account/google' ||
      false
  )
  const [Profile, setProfile] = React.useState(location === 'Profile' || false)
  const [Bookings, setBookings] = React.useState(
    location === 'Bookings' || false
  )
  const [Packages, setPackages] = React.useState(
    location === 'Packages' || false
  )
  const [Organize, setOrganize] = React.useState(
    location === 'Organize' || false
  )
  const [Organizations, setOrganizations] = React.useState(
    location === 'Organizations' || false
  )
  const [Courses, setCourses] = React.useState(location === 'Courses' || false)

  const verifiedState = useSelector((state) => state.verified)
  const [verified, setVerified] = React.useState(
    verifiedState !== '' && verifiedState !== 'verified'
  )
  let pleaseVerify = []
  if (verified) {
    pleaseVerify.push(
      <td>
        <tr
          style={{
            justifyContent: 'center',
            display: 'flex',
            cursor: 'pointer',
          }}
          onClick={() => console.log('here')}
        >
          <Typography
            style={{
              fontSize: '1vw',
              fontWeight: 'bold',
              textAlign: 'center',
            }}
          >
            <img
              src={verify}
              style={{
                width: '1vw',
                color: '#ED1C24',
                marginRight: '0.2vw',
                cursor: 'pointer',
              }}
            />
            Account is not verified !
          </Typography>
        </tr>
        <Typography
          style={{ fontSize: '1vw', textAlign: 'center', cursor: 'pointer' }}
        >
          Please verify your phone number
        </Typography>
      </td>
    )
  }
  const token = useSelector((state) => state.token)
  const id = useSelector((state) => state.id)
  const emailFromRedux = useSelector((state) => state.email)
  const [firstName, setFirstName] = useState('')
  const [lastName, setLastName] = useState('')

  useEffect(() => {
    if (token === '') {
      history.push('/home/SignIn')
    } else if (!dispatch(checkTokenExpired(history))) {
      axios({
        url: `${backendLink}/tbhapp/accounts/getprofile`,
        method: 'POST',
        headers: {
          authorization: token,
        },
        data: {
          Account: { id },
        },
      }).then((res) => {
        console.log(res)
        if (res.data.statusCode === 0) {
          setUsername(res.data.profile.username)
          setEmail(res.data.profile.email)
          setFirstName(res.data.profile.firstName)
          setLastName(res.data.profile.lastName)
          dispatch(setEmailRedux(res.data.profile.email))
        }
      })
      setVerified(verifiedState !== '' && verifiedState !== 'verified')
    }
    console.log(pathname)
    setAccount(
      location === 'Account' ||
        pathname === '/Account/facebook' ||
        pathname === '/Account/google' ||
        false
    )
    setProfile(location === 'Profile' || false)
    setBookings(location === 'Bookings' || false)
    setPackages(location === 'Packages' || false)
    setOrganize(location === 'Organize' || false)
    setOrganizations(location === 'Organizations' || false)
    setCourses(location === 'Courses' || false)
  }, [verifiedState, emailFromRedux, pathname])
  const handleAccount = () => {
    setAccount(true)
    setProfile(false)
    setBookings(false)
    setPackages(false)
    setOrganize(false)
    setOrganizations(false)
    setCourses(false)
  }
  const handleProfile = () => {
    setAccount(false)
    setProfile(true)
    setBookings(false)
    setPackages(false)
    setOrganize(false)
    setOrganizations(false)
    setCourses(false)
  }
  const handleBookings = () => {
    setAccount(false)
    setProfile(false)
    setBookings(true)
    setPackages(false)
    setOrganize(false)
    setOrganizations(false)
    setCourses(false)
  }
  const handlePackages = () => {
    setAccount(false)
    setProfile(false)
    setBookings(false)
    setPackages(true)
    setOrganize(false)
    setOrganizations(false)
    setCourses(false)
  }
  const handleOrganize = () => {
    setAccount(false)
    setProfile(false)
    setBookings(false)
    setPackages(false)
    setOrganize(true)
    setOrganizations(false)
    setCourses(false)
  }
  const handleOrganizations = () => {
    setAccount(false)
    setProfile(false)
    setBookings(false)
    setPackages(false)
    setOrganize(false)
    setOrganizations(true)
    setCourses(false)
  }
  const handleCourses = () => {
    setAccount(false)
    setProfile(false)
    setBookings(false)
    setPackages(false)
    setOrganize(false)
    setOrganizations(false)
    setCourses(true)
  }
  return (
    <div>
      <table>
        <td>
          <div
            style={{
              width: '25vw',
              height: '100vh',
              display: 'flex',
              justifyContent: 'center',
              position: 'sticky',
              top: 0,
              borderColor: 'black',
              boxShadow:
                '0 -6px white, 0 6px white, 6px 0 4px -4px #D2D1D1, -7px 0 4px 0px grey',
              marginBottom: '1vw',
            }}
          >
            <table>
              <tr
                style={{
                  justifyContent: 'center',
                  display: 'flex',
                  color: '#ED1C24',
                }}
              >
                {pleaseVerify}
              </tr>
              <tr>
                <Avatar
                  style={{
                    color: 'white',
                    backgroundColor: 'black',
                    marginTop: '2vw',
                    marginLeft: 'auto',
                    marginRight: 'auto',
                    width: '3vw',
                    height: '3vw',
                    fontSize: '2vw',
                    fontWeight: '500',
                  }}
                >
                  {username.substring(0, 1).toUpperCase()}
                </Avatar>
              </tr>
              <tr>
                <div
                  style={{
                    marginTop: '2vw',
                    color: '#0F888E',
                    fontSize: '1vw',
                    textAlign: 'center',
                    marginLeft: 'auto',
                    marginRight: 'auto',
                    fontWeight: '500',
                  }}
                >
                  {firstName} {lastName}
                </div>
              </tr>
              <tr>
                <div
                  style={{
                    marginTop: '2vw',
                    color: '#767676',
                    fontSize: '1vw',

                    width: '100%',
                    textAlign: 'center',
                  }}
                >
                  {email}
                </div>
              </tr>
              <tr>
                <div
                  style={{
                    marginTop: '2vw',
                    backgroundColor: '#767676',
                    height: '0.05vw',
                    width: '20vw',
                  }}
                ></div>
              </tr>
              <tr>
                <td>
                  {Account ? (
                    <div
                      style={{
                        backgroundColor: 'red',
                        width: '0.2vw',
                        height: '2vw',
                        marginTop: '3vw',
                        marginLeft: '4vw',
                      }}
                    ></div>
                  ) : (
                    <div></div>
                  )}
                </td>
                <td>
                  <div
                    onClick={handleAccount}
                    style={{
                      marginTop: '3vw',
                      color: Account ? 'red' : 'black',
                      fontWeight: Account ? 'bold' : 'normal',
                      fontSize: Account ? '1.2vw' : '1vw',
                      width: '10vw',
                      marginLeft: '-15vw',
                      textAlign: 'left',
                      cursor: 'pointer',
                      fontWeight: '500',
                    }}
                  >
                    Account Settings
                  </div>
                </td>
              </tr>
              <tr>
                <td>
                  {Profile ? (
                    <div
                      style={{
                        backgroundColor: 'red',
                        width: '0.2vw',
                        height: '2vw',
                        marginTop: '2vw',
                        marginLeft: '4vw',
                      }}
                    ></div>
                  ) : (
                    <div></div>
                  )}
                </td>
                <div
                  onClick={handleProfile}
                  style={{
                    marginTop: '2vw',
                    fontWeight: Profile ? 'bold' : 'normal',
                    color: Profile ? 'red' : 'black',
                    fontSize: Profile ? '1.2vw' : '1vw',
                    width: '10vw',
                    marginLeft: '-15vw',
                    textAlign: 'left',
                    cursor: 'pointer',
                    fontWeight: '500',
                  }}
                >
                  Profile Settings
                </div>
              </tr>
              <tr>
                <td>
                  {Bookings ? (
                    <div
                      style={{
                        backgroundColor: 'red',
                        width: '0.2vw',
                        height: '2vw',
                        marginTop: '2vw',
                        marginLeft: '4vw',
                      }}
                    ></div>
                  ) : (
                    <div></div>
                  )}
                </td>
                <div
                  onClick={handleBookings}
                  style={{
                    marginTop: '2vw',
                    color: Bookings ? 'red' : 'black',
                    fontSize: Bookings ? '1.2vw' : '1vw',
                    fontWeight: Bookings ? 'bold' : 'normal',
                    width: '10vw',
                    marginLeft: '-15vw',
                    textAlign: 'left',
                    cursor: 'pointer',
                    fontWeight: '500',
                  }}
                >
                  My Bookings
                </div>
              </tr>
              <tr>
                <td>
                  {Packages ? (
                    <div
                      style={{
                        backgroundColor: 'red',
                        width: '0.2vw',
                        height: '2vw',
                        marginTop: '2vw',
                        marginLeft: '4vw',
                      }}
                    ></div>
                  ) : (
                    <div></div>
                  )}
                </td>
                <div
                  onClick={handlePackages}
                  style={{
                    marginTop: '2vw',
                    color: Packages ? 'red' : 'black',
                    fontSize: Packages ? '1.2vw' : '1vw',
                    fontWeight: Packages ? 'bold' : 'normal',
                    width: '10vw',
                    marginLeft: '-15vw',
                    textAlign: 'left',
                    cursor: 'pointer',
                    fontWeight: '500',
                  }}
                >
                  My Packages
                </div>
              </tr>
              <tr>
                <td>
                  {Organize ? (
                    <div
                      style={{
                        backgroundColor: 'red',
                        width: '0.2vw',
                        height: '2vw',
                        marginTop: '2vw',
                        marginLeft: '4vw',
                      }}
                    ></div>
                  ) : (
                    <div></div>
                  )}
                </td>
                <div
                  onClick={handleOrganize}
                  style={{
                    marginTop: '2vw',
                    color: Organize ? 'red' : 'black',
                    fontSize: Organize ? '1.2vw' : '1vw',
                    fontWeight: Organize ? 'bold' : 'normal',
                    width: '10vw',
                    marginLeft: '-15vw',
                    textAlign: 'left',
                    cursor: 'pointer',
                    fontWeight: '500',
                  }}
                >
                  Organize With Us
                </div>
              </tr>
            </table>
          </div>
        </td>
        <td>
          {Account ? (
            <AccountSettings />
          ) : Profile ? (
            <ProfileSettings />
          ) : Organize ? (
            <OrganizePage />
          ) : Bookings ? (
            <BookingsPage />
          ) : Packages ? (
            <MyPackages />
          ) : null}
        </td>
      </table>
    </div>
  )
}
