  const googleSignUpProcedure = async () => {
    let username = ''

    axios({
      url: `${backendLink}/tbhapp/accounts/googlecallback` + '?code=' + code,
      method: 'POST',
      headers: {},
      data: {
        state: 'signUp',
      },
    }).then(async (res) => {
      if (res.data.info.userData && !res.data.info.error) {
        await axios({
          method: 'post',
          url: `${backendLink}/tbhapp/accounts/generateUsername`,
          data: {
            Account: {
              firstName: res.data.info.userData.given_name,
            },
          },
        }).then(async (res1) => {
          if (res1.data.statusCode === 0) {
            username = res1.data.username
          }
        })
        setState((prevState) => {
          return {
            ...prevState,
            firstName: res.data.info.userData.given_name,
            lastName: res.data.info.userData.family_name,
            email: res.data.info.userData.email,
            id: res.data.info.userData.id,
            username: username,
          }
        })
      }
    })
  }

