import React from 'react'
import { makeStyles } from '@material-ui/core/styles'
import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
import DialogContentText from '@material-ui/core/DialogContentText'
import DialogTitle from '@material-ui/core/DialogTitle'
import { Container, tr, td } from 'react-bootstrap'
import Table from 'react-bootstrap/Table'
import CloseButton from '@material-ui/icons/Cancel'
import CloseIcon from '@material-ui/icons/Close'
import IconButton from '@material-ui/core/IconButton'
import axios from 'axios'
import Form from 'react-bootstrap/Form'
import headerLayouts from '../images/headerLayouts.png'

import { Divider, Typography } from '@material-ui/core'
import { useSelector, useDispatch } from 'react-redux'
import {
  checkTokenExpired,
  resetToken,
} from '../globalState/actions/AuthActions'
import { useHistory } from 'react-router'
import ReactDOM from 'react-dom'
import Countdown from 'react-countdown'
import { changeVerified } from '../globalState/actions/AuthActions'
import { backendLink } from '../keys_dev'

const useStyles = makeStyles({
  paper: {
    minWidth: '31vw',
    width: '31vw',

    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    backgroundColor: 'white',
    borderRadius: '0',
    overflow: 'hidden',
    paddingTop: '0',
  },
  exitButton: {
    color: 'black',
    height: '2vw',
    width: '2vw',
    marginRight: '1vw',
  },
  notClickedButton: {
    color: 'black',
    height: '2.5vw',
    width: '3.2vw',
    boxShadow: 'none !important',
    fontSize: '1vw',
    '&:hover': {
      border: 'none',
      boxShadow: 'none',
      outline: 'none',
    },
    '&:focus': {
      border: 'none',
      boxShadow: 'none',
      outline: 'none',
    },
    '&:active': {
      boxShadow: 'none !important',
      outline: 'none !important',
    },
  },
})

export default function ValidationModal(props) {
  const token = useSelector((state) => state.token)
  const dispatch = useDispatch()
  const history = useHistory()

  const id = useSelector((state) => state.id)
  let openClose = props.open
  const classes = useStyles()
  const [count, setCount] = React.useState(false)

  const [showAlert, setShowAlert] = React.useState(false)
  const [date, setDate] = React.useState(Date.now() + 120000)
  const [showAlertMess, setShowAlertMess] = React.useState('error')
  const [verificationMessage, setVerificationMessage] = React.useState(
    'VERIFICATION'
  )
  const [code, setCode] = React.useState('')
  const [errorMessage, setErrorMessage] = React.useState('')
  const handleChange = (e) => {
    setCode(e.target.value)
  }

  const Completionist = () => {
    return (
      <span
        style={{
          color: '#ED1C24',
          paddingTop: '0.3vw',
          fontSize: '0.5vw',
          cursor: 'pointer',
        }}
        onClick={() => {
          setDate(Date.now() + 120000)
          setCount(!count)
          handleRetry()
        }}
      >
        {' '}
        RESEND CODE
      </span>
    )
  }

  // Renderer callback with condition
  const renderer = ({ hours, minutes, seconds, completed }) => {
    if (completed) {
      setDate(Date.now() + 120000)
      setCount(!count)
      // Render a completed state
      return <Completionist />
    } else {
      // Render a countdown
      return (
        <span>
          {('0' + minutes).slice(-2)}:
          {seconds < 10 ? ('0' + seconds).slice(-2) : seconds}
        </span>
      )
    }
  }
  const handleClick = () => {
    if (!dispatch(checkTokenExpired(history))) {
      axios({
        url: `${backendLink}/tbhapp/accounts/confirmverify`,
        method: 'POST',
        headers: {
          authorization: token,
        },
        data: {
          Account: { id: id, code: code },
        },
      }).then((res) => {
        console.log(res)
        if (res.data.statusCode === 0) {
          dispatch(changeVerified('verified'))
          dispatch(resetToken(token))
          props.handleClose()
        } else {
          if (res.data.statusCode === 112) {
            setVerificationMessage('VERIFICATION FAILED')
            setErrorMessage('* Code has expired, please resend the code')
          } else {
            setVerificationMessage('VERIFICATION FAILED')
            setErrorMessage('* Code is incorrect, Please try again.')
          }
        }
      })
    }
  }

  const handleRetry = () => {
    if (!dispatch(checkTokenExpired(history))) {
      axios({
        url: `${backendLink}/tbhapp/accounts/verify`,
        method: 'POST',
        headers: {
          authorization: token,
        },
        data: {
          Account: { id: id },
        },
      }).then((res) => {
        console.log(res)

        setDate(Date.now() + 120000)
        setVerificationMessage('VERIFICATION')
        setErrorMessage('')
      })
    }
  }
  return (
    <div>
      <Dialog
        open={true}
        aria-labelledby='alert-dialog-title'
        aria-describedby='alert-dialog-description'
        classes={{ paper: classes.paper }}
      >
        <DialogTitle
          style={{
            width: '100%',
            height: ' 2vw',
            display: 'flex',
            padding: 0,
            justifyContent: 'flex-end',
          }}
        >
          <IconButton
            aria-label='close'
            className={classes.exitButton}
            onClick={() => {
              props.handleClose(false)
            }}
          >
            <CloseIcon />
          </IconButton>
        </DialogTitle>
        <DialogContent
          style={{
            width: '100%',
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'bottom',
            padding: '0vw',
          }}
        >
          <table>
            <tr
              style={{
                fontWeight: '500',
                color: '#ED1C24',
                fontSize: '1.5vw',
                height: '2vw',
                display: 'flex',
                justifyContent: 'center',
              }}
            >
              {verificationMessage}
            </tr>
            <tr
              style={{
                color: '#7E7E7E',
                fontWeight: '500',
                paddingRight: '7vw',
                paddingLeft: '7vw',
                paddingTop: '0.6vw',
                fontSize: '0.7vw',
                display: 'flex',
                justifyContent: 'center',
                textAlign: 'center',
              }}
            >
              We have sent a verification code to your phone number, please
              enter the code sent via sms. The code expires in 24 hours.
            </tr>
            <tr>
              <div style={{ display: 'flex' }}>
                <td
                  style={{
                    paddingTop: '1vw',
                    width: '25vw',
                    fontSize: '0.7vw',
                    display: 'flex',
                    justifyContent: 'flex-end',
                    textAlign: 'center',
                    borderColor: '#ED1C24',
                    margin: '0',
                  }}
                >
                  <div
                    style={{
                      backgroundColor: '#ED1C24',
                      width: '0.3vw',
                      marginRight: '0.5vw',
                      height: '3vw',
                    }}
                  ></div>
                  <Form style={{ height: '7vw' }}>
                    <Form.Group style={{ height: '3vw' }}>
                      <Form.Control
                        style={{
                          width: '18vw',
                          height: '3vw',
                          borderRadius: '0',
                          fontSize: '1vw',
                          borderColor: '#ED1C24',
                          borderWidth: '0.1vw',
                        }}
                        type='email'
                        placeholder='ENTER THE CODE'
                        onChange={handleChange}
                      />
                      <Form.Text
                        style={{
                          height: '1vw',
                          color: '#ED1C24',
                          fontSize: '0.9vw',
                          fontWeight: '600',
                          display: 'flex',
                        }}
                      >
                        {errorMessage}
                      </Form.Text>
                    </Form.Group>
                  </Form>
                </td>

                <td
                  style={{
                    width: '4vw',
                    marginTop: '1.7vw',
                    paddingLeft: '0.4vw',
                    fontSize: '1vw',
                    fontWeight: '700',
                    display: 'flex',
                  }}
                >
                  {count ? (
                    <Countdown
                      zeroPadTime={2}
                      date={date}
                      renderer={renderer}
                    />
                  ) : (
                    <Completionist />
                  )}
                </td>
              </div>
            </tr>
          </table>
        </DialogContent>
        <DialogActions
          style={{
            width: '31',
            display: 'flex',
            justifyContent: 'center',
          }}
        >
          <Button
            variant='danger'
            style={{
              fontSize: '0.9vw',
              fontWeight: '600',
              color: 'white',
              width: '7vw',
              height: '2.5vw',
              borderRadius: '0',
              backgroundColor: '#ED1C24',
            }}
            type='submit'
            onClick={handleClick}
          >
            VERIFY
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  )
}
