export default function WelcomeSignIn(props) {
  const [code, setCode] = React.useState(
    queryString.parse(useLocation().search).code
  )
  const { pathname } = useLocation()
  const [SignIn, setSignIn] = React.useState(false)
  const [validationPue, setValidationPue] = React.useState('')
  const [validationPassword, setValidationPassword] = React.useState('')
  const [validated, setValidated] = React.useState(false)
  const [forgot, setForgot] = React.useState(false)
  const [state, setState] = React.useState({})
  const classes = useStyles()
  const dispatch = useDispatch()
  const history = useHistory()
  useEffect(() => {
    if (code) {
      if (pathname === '/google/SignIn') {
        googleSignInProcedure()
      } else if (pathname === '/facebook/SignIn') {
        facebookSignInProcedure()
      }
    }
  }, [])

  const handleChange = (e) => {
    const name = e.target.name
    const value = e.target.value
    setState((prevState) => {
      return {
        ...prevState,
        [name]: value,
      }
    })
  }

  const handleSignIn = async (event) => {
    const form = event.currentTarget
    if (form.checkValidity() === false) {
      setValidationPue('')
      setValidationPassword('')
      event.preventDefault()
      event.stopPropagation()
      setValidated(true)
    } else {
      event.preventDefault()
      const x = await dispatch(loginAPI(state.pue, state.password, history))
      if (x !== 0) {
        setValidationPue('* invalid credentials')
        setValidationPassword('* invalid credentials')
        setValidated(false)
        event.preventDefault()
        event.stopPropagation()
      }
    }
  }
  const rightSide = () => {
    return (
      <div>
        {forgot ? <ForgotModal handleClose={() => setForgot(false)} /> : null}
        <Row>
          <img
            src={xsign}
            className={classes.xsignStyle}
            alt="exit"
            onClick={() => props.handleSignIn(false)}
          />
        </Row>
        <Row>
          <text className={classes.signInHeader}>Sign In</text>
        </Row>
        <Form noValidate validated={validated} onSubmit={handleSignIn}>
          <Form.Group>
            <Form.Control
              className={classes.formControl}
              name="pue"
              onChange={handleChange}
              required={validationPue === ''}
              value={state['pue'] !== undefined ? state['pue'] : ''}
              placeholder="E-Mail/Username/Phone Number"
            ></Form.Control>
            <Row>
              <text className={classes.errorValidation}>{validationPue}</text>
            </Row>
          </Form.Group>
          <Form.Group>
            <Form.Control
              className={classes.formControl}
              name="password"
              type="password"
              onChange={handleChange}
              required={validationPassword === ''}
              value={state['password'] !== undefined ? state['password'] : ''}
              placeholder="Password"
            ></Form.Control>
            <Row>
              <text className={classes.errorValidation}>
                {validationPassword}
              </text>
            </Row>
          </Form.Group>
          <Form.Group>
            <Row>
              <div onClick={() => setForgot(true)} className={classes.text2}>
                Forgot your password?
              </div>
            </Row>
          </Form.Group>
          <Row>
            <Button className={classes.SignInButton} type="submit">
              {' '}
              SIGN IN
            </Button>
          </Row>
        </Form>
        <Table borderless>
          <tbody>
            <tr>
              <td rowSpan={2}>
                {' '}
                <Row>
                  <img
                    src={lowerLeft}
                    className={classes.lowerLeftImage}
                    alt="red and blue boxes"
                  />
                </Row>
              </td>
            </tr>
            <tr>
              <td>
                <Row>
                  {' '}
                  <text className={classes.googleOrFbText}>
                    Or you can Sign in with{' '}
                    <img
                      style={{ cursor: 'pointer' }}
                      src={google}
                      className={classes.googleImage}
                      alt="google"
                      onClick={() => {
                        axios({
                          url: `${backendLink}/tbhapp/accounts/getgoogleurl`,
                          method: 'POST',
                          headers: {},
                          data: {
                            state: 'signIn',
                          },
                        }).then((res) => {
                          if (res.data.url) {
                            setSignIn(true)
                            window.location.replace(res.data.url)
                          }
                        })
                      }}
                    />{' '}
                    or{' '}
                    <img
                      src={facebook}
                      className={classes.fbImage}
                      alt="facebook"
                      onClick={() => {
                        axios({
                          url: `${backendLink}/tbhapp/accounts/getfacebookurl`,
                          method: 'POST',
                          headers: {},
                          data: {
                            state: 'signIn',
                          },
                        }).then((res) => {
                          if (res.data) {
                            window.location.replace(res.data)
                          }
                        })
                      }}
                    />{' '}
                    or{' '}
                    <img
                      style={{ cursor: 'pointer' }}
                      src={lirten}
                      className={classes.lirten}
                      alt="lirten"
                      onClick={() => {
                        const redirectLink = `${lirtenFrontEndLink}/signin/tbh`
                        window.open(redirectLink, '_self')
                        // window.location.replace(lirtenFrontEndLink)
                      }}
                    />
                  </text>
                </Row>
              </td>
            </tr>
          </tbody>
          {/*
           */}
        </Table>
      </div>
    )
  }
  const leftSide = () => {
    return (
      <div>
        <Row>
          <img
            src={upperRight}
            className={classes.upperRightImage}
            alt="red and black boxes"
          />
        </Row>
        <Row>
          <text className={classes.headerText}>WELCOME BACK</text>
        </Row>
        <Row>
          <text className={classes.subHeaderText}>
            To keep connected to us <br></br>please use your personal
            information
          </text>
        </Row>
        <Row>
          <text className={classes.or}>OR</text>
        </Row>
        <Row>
          <text className={classes.text1}>CREATE A NEW ACCOUNT</text>
        </Row>
        <Row>
          <Button
            className={classes.signUpButton}
            onClick={() => {
              props.handleSignUp(true)
            }}
          >
            {' '}
            SIGN UP
          </Button>
        </Row>
      </div>
    )
  }

  const googleSignInProcedure = async () => {
    axios({
      url: `${backendLink}/tbhapp/accounts/googlecallback` + '?code=' + code,
      method: 'POST',
      headers: {},
      data: {
        state: 'signIn',
      },
    }).then(async (res) => {
      if (res.data.info.userData) {
        const x = await dispatch(
          loginGoogle(res.data.info.userData.id, history)
        )
        if (x !== 0) {
          setValidationPue('* invalid credentials')
          setValidationPassword('* invalid credentials')
        }
      }
    })
  }

  const facebookSignInProcedure = async () => {
    axios({
      url: `${backendLink}/tbhapp/accounts/facebookcallback` + '?code=' + code,
      method: 'POST',
      headers: {},
      data: {
        state: 'signIn',
      },
    }).then(async (res) => {
      if (res.data.statusCode === 0) {
        const x = await dispatch(
          loginFacebook(res.data.data.facebookId, history)
        )
        if (x !== 0) {
          setValidationPue('* invalid credentials')
          setValidationPassword('* invalid credentials')
        }
      }
    })
  }

  return (
    <div>
      <div id="outerDiv" className={classes.overlay}>
        <div
          style={{
            position: 'absolute',
            top: '50%',
            left: '50%',
            backgroundColor: 'white',
            color: 'white',
            transform: 'translate(-50%,-50%)',
            width: '70.93vw',
            border: '0.1vw solid #ec2227',
            borderRadius: '0vw 0.5vw 0.5vw 0vw',
            marginRight: '0px',
          }}
        >
          <Table
            style={{
              height: '100%',
              marginBottom: '0px',
              marginRight: '0px',
              width: '100%',
              maxWidth: 'unset',
              padding: '0px',
              borderTop: '0px',
            }}
          >
            <tbody
              style={{ height: '100%', marginRight: '0px', padding: '0px' }}
            >
              <td
                style={{
                  backgroundColor: '#EC2227',
                  width: '1.5vw',
                  border: '0.06vw solid #EC2227',
                  paddingLeft: '0px',
                  paddingRight: '0px',
                  marginRight: '0px',
                  padding: '0px',
                  borderTop: '0px',
                }}
              ></td>
              <td style={{ padding: '0', borderTop: '0' }}>
                <Table style={{ maxWidth: 'unset' }}>
                  <tbody>
                    <tr className={classes.td}>
                      <td className={classes.leftCol}>{leftSide()}</td>
                      <td style={{ border: '0px' }}>
                        <tr>
                          <p> </p>
                        </tr>
                        <tr>
                          <td className={classes.td}>{rightSide()}</td>
                        </tr>
                      </td>
                    </tr>
                  </tbody>
                </Table>
              </td>
            </tbody>
          </Table>
        </div>
      </div>
    </div>
  )
}
